import React, { FC, useCallback } from 'react';
import { NavLink, useLocation } from 'react-router-dom';
import clsx from 'clsx';

import { useAppDispatch } from '../../services/hooks';
import { logOutUser } from '../../services/thunks/user';

import styles from './navbar.module.css';

const Navbar: FC = () => {
  const location = useLocation();
  const dispatch = useAppDispatch();

  const onClickHandler = useCallback(() => {
    dispatch(logOutUser());
  }, [dispatch]);
  
  return (
    <aside className={clsx(styles.aside, 'mr-15')}>
      <nav className='mb-20'>
        <ul className={styles.list}>
          <li className={styles['list__item']}>
            <NavLink to="/profile" end className={styles.link}>
              {({ isActive }) => (
                <span
                  className={clsx(
                    'text',
                    'text_type_main-medium',
                    !isActive && 'text_color_inactive'
                  )}
                >
                  
                </span>
              )}
            </NavLink>
          </li>
          <li className={styles['list__item']}>
            <NavLink to="/profile/orders" className={styles.link}>
              {({ isActive }) => (
                <span
                  className={clsx(
                    'text',
                    'text_type_main-medium',
                    !isActive && 'text_color_inactive'
                  )}
                >
                   
                </span>
              )}
            </NavLink>
          </li>
          <li
            className={clsx(
              styles['list__item'],
              'text',
              'text_type_main-medium',
              'text_color_inactive'
            )}
          >
            <button className={styles['exit-btn']} onClick={onClickHandler}></button>
          </li>
        </ul>
      </nav>
      {location.pathname === '/profile' && (
        <span
          className={clsx(
            'text',
            'text_type_main-default',
            'text_color_inactive'
          )}
        >
                  
        </span>
      )}
      {location.pathname === '/profile/orders' && (
        <span
          className={clsx(
            'text',
            'text_type_main-default',
            'text_color_inactive'
          )}
        >
                  
        </span>
      )}
    </aside>
  );
}

export default Navbar;
